@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (min-width: 800px){.body-layout{display:flex}}

.persons-list{list-style:none;margin:8px 0;padding:0;max-height:160px;overflow:scroll}@media (min-width: 800px){.persons-list{max-height:400px}}.persons-list__person{padding:4px 8px}.persons-list__person:nth-child(even){background-color:#e4e4e4}

.invalid{border:1px solid red}

